:root {
  --font-inter: "Inter", sans-serif;
  --font-lexendDeca: "Lexend Deca", sans-serif;
}

:root {
  --main-bg-color: hsl(233, 47%, 7%);
  --card-bg-color: hsl(244, 38%, 16%);
  --accent: hsl(277, 64%, 61%);
  --main-heading-stats-color: hsl(0, 0%, 100%);
  --main-paragraph-color: hsl(0, 0%, 100%);
  --stat-headings-color: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

main {
  background-color: var(--main-bg-color);
  padding: 5.3125rem 1.25rem;
  text-align: center;
}
@media (min-width: 40em) {
  main {
    padding: 10.9375rem 10.3125rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--main-heading-stats-color);
}

p {
  color: var(--stat-headings-color);
  margin-bottom: 35px;
}
p.p-stats {
  text-transform: uppercase;
  font-size: 12px;
}

.highlighted {
  color: var(--accent);
}

.main-container {
  border-radius: 0.625rem;
  background-color: var(--card-bg-color);
  min-width: 12.5rem;
}
@media (min-width: 64em) {
  .main-container {
    text-align: left;
    max-width: 69.6875rem;
    min-height: 27.8125rem;
    display: flex;
    flex-direction: row-reverse;
  }
}

.img-header {
  min-height: 15rem;
  background: url("../../images/image-header-mobile.jpg") var(--accent);
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
}
@media (min-width: 40em) {
  .img-header {
    background: url("../../images/image-header-desktop.jpg") var(--accent);
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
}

.main-content {
  padding: 1.875rem;
}
@media (min-width: 64em) {
  .main-content {
    padding: 5rem;
  }
}

.column {
  flex: 50%;
}

@media (min-width: 64em) {
  .p-info {
    margin-bottom: 9.375rem;
  }
}

@media (min-width: 64em) {
  .content-stats {
    border: 2px solid red;
    display: flex;
    justify-content: space-between;
    max-height: 3.75rem;
    max-width: 22.5rem;
  }
}